export default {
    async getPublicData() {
        const globalApiRequest = await fetch('https://globalapi.dodopizza.com/api/v1/pizzerias/count')
        const countriesCountData = await globalApiRequest.json()
        return {
            countriesCount: countriesCountData.countries.length,
            totalCount: countriesCountData.countries.reduce((c, n) => c + n.pizzeriaCount, 0)
        }
    }
}